                          >Apply Now</v-btn
                        >
                      </v-col>
                    </v-row>
                  </v-col>
                </v-row>
              </v-card-actions>
            </template>
            <v-alert v-else text outlined color="deep-orange" icon="mdi-cancel">
              Job not found
              <v-divider
                class="my-4 deep-orange"
