            <v-list rounded nav dense>
              <v-list-item-group
                v-if="isLoggedIn && userInfo.id === data.user.id"
                color="deep-orange"
              >
                <v-list-item @click="$router.push(`/jobs/${data.id}/edit`)">
                  <v-list-item-icon
                    ><v-icon
                      >mdi-file-document-edit-outline</v-icon
                    ></v-list-item-icon
                  >
                  <v-list-item-content>
